<template>
  <div class="container">
    <div style="">
      <!-- 分类选择 -->
      <div class="category">
        <div>
          <el-select v-model="cateInputValue" @change="GetSelCate" placeholder="请选择分类">
            <el-option
              v-for="item in tableDataSelect"
              :key="item.tag"
              :label="item.name"
              :value="item"
              clearable="true">
            </el-option>
          </el-select>
        </div>
        <div class="cate-edit-but">
          <el-row>
            <el-button @click="cateEditBut" type="primary" plain>{{cateEditButContent}}</el-button>
          </el-row>
        </div>
      </div>
      <!-- 分类编辑弹框 -->
      <div v-if="cateEditIsOpen" class="cate-edit-page">
        <!-- 分类编辑 -->
        <el-table
          :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
          style="width: 100%"
          ref="singleTable">
          <el-table-column
            label="Name"
            prop="name">
          </el-table-column>
          <el-table-column
            align="right">  
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder="输入关键字搜索"/>
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)">Edit</el-button>
              <el-popconfirm 
              title="确定删除？" @onConfirm="delCate">
                <el-button style="margin-left: 10px;" @click="handleDelete(scope.$index, scope.row)" size="mini" type="danger" slot="reference">Delete</el-button>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
          <div class="get-nav-input">
            <el-select v-model="navInputValue" placeholder="请选择顶级导航">
              <el-option
                v-for="item in navOptions"
                :key="item.tag"
                :label="item.name"
                :value="item.tag"
                size="mini">
              </el-option>
            </el-select>
          </div>
        <div class="create-category">
          <div class="create-input">
            <el-input
              placeholder="请输入分类名"
              v-model="crateCateInput"
              size="small"
              clearable>
            </el-input>
          </div>
          <div class="create-but">
            <el-button @click="postCategory" size="small" type="primary" plain>创建</el-button>
            <!-- <el-button type="text" @click="open">创建分类</el-button> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { PostCategory } from '@/api/api'
import { DelCategory } from '@/api/api'
import { GetCategory } from '@/api/api'
import { GetNavigation } from '@/api/api'
import util from '../../../utils/utils'

export default {
  data() {
    return {
      navOptions: [],
      navInputValue:'', // 选中的顶级导航数据
      cateInputValue: '', // 选中的分类数据
      cateEditButContent: '编辑',
      crateCateInput: '',
      delCateContent: '', // 要删除的分类名称
      cateEditIsOpen: false,
      tableData: [], // 分类列表数据
      tableDataSelect: [], // 分类选择框的列表数据
      search: '', // 分类列表搜索框
    }
  },
  created() {
    // 编辑文章时获取的分类名称, 由Editor.vue触发
    util.$on('setCateInputValue', (item) => {
      this.cateInputValue = item.category_name
    })
    // 通过导航tag获取分类, 由Navigation.vue触发
    util.$on('getCateByNav', (item) => {
      this.getCategoryByNav(item)
    })
    // 点击导航选择框时清空分类, 由Navigation.vue触发
    util.$on('clearCateByNav', () => {
      this.cateInputValue = ''
    })
    
    // 弹框是获取分类列表的数据 不是选择框
    this.getCategory()

  },
  methods: {
    // 分类列表, 编辑按钮
    handleEdit(index, row) {
      console.log(index, row);
      //this.open2(row.name);
    },
    // 分类列表, 删除按钮
    handleDelete(index, row) {
      this.delCateContent = row
      //console.log(index, row);
    },
    delCate(){
      DelCategory(this.delCateContent.id).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          this.successMsg()
          this.getCategory()
        } else {
          this.errMsg('', response.data.msg)
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // 分类编辑弹框按钮
    cateEditBut() {
      this.getNavigation() // 打开分类编辑弹框是获取顶级导航

      if (this.cateEditIsOpen == true) {
        this.cateEditButContent = '编辑'
        this.cateEditIsOpen = false
        return
      }
      if (this.cateEditIsOpen == false) {
        this.cateEditButContent = '关闭'
        this.cateEditIsOpen = true
        return
      }
    },
    // 获取导航
    getNavigation() {
      GetNavigation().then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          if (response.data.data.navigation == null) {
            this.navOptions = []
            return
          }
          this.navOptions = response.data.data.navigation
          return
        }
      }).catch(err => {
        this.errMsg('获取导航列表时出错', err)
      })
    },
    // 修改分类弹框
    open2(inputValue) {
      this.$prompt('请输入要修改的分类名', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputValue: inputValue
      }).then(({ value }) => {
        if ( value == '' ) { this.errMsg('不可为空','') } 
        else { console.log(value) }
      }).catch(() => {
        this.cancelMsg()
      });
    },
    // 获取分类by顶级导航
    getCategoryByNav(navigationTag) {
      GetCategory(navigationTag).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          if (response.data.data.category == null) {
            this.tableDataSelect = []
            return
          }
          this.tableDataSelect = response.data.data.category
          return
        }
      }).catch(err => {
        this.errMsg('获取分类列表时出错', err)
      })
    },
    // 获取分类
    getCategory() {
      GetCategory('').then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          if (response.data.data.category == null) {
            this.tableData = []
            return
          }
          this.tableData = response.data.data.category
          return
        }
      }).catch(err => {
        this.errMsg('获取分类列表时出错', err)
      })
    },
    // 创建分类
    postCategory() {
      var crateCateInput = this.crateCateInput
      var navInputValue  = this.navInputValue

      if ( navInputValue == '' ) { this.errMsg('请选择顶级导航',''); return }
      if ( crateCateInput == '' ) { this.errMsg('分类名不可为空',''); return }

      PostCategory(crateCateInput, navInputValue).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          this.crateCateInput = ''
          this.successMsg()
          this.getCategory('')
          this.getCategoryByNav(this.navInputValue)
        } else {
          this.errMsg('', response.data.msg)
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // 获取选中的分类tag //传给父组件
    GetSelCate: function(item) {
      this.cateInputValue = item.name   // 选框的显示值,  疑问itme是哪里来的 上面并没有传
      this.$emit('GetSelCate', item);  // 传给父组件
    },
    // 提示消息
    successMsg() {
      this.$message({
        type: 'success',
        message: '操作成功'
      });
    },
    errMsg(msg, err) {
      this.$message({
        type: 'error',
        message: msg + err
      });
    },
    cancelMsg() {
      this.$message({
        type: 'info',
        message: '取消输入'
      });
    }
  },
}
</script>

<style scoped>
.container {
  width: 100%;
}
.category {
  display: flex;
  margin-bottom: 20px;
}
.cate-edit-but {
  /* div内的内容垂直居中 display: flex; align-items: center;*/
  display: flex;
  align-items: center;
  margin-left: 5px
}
.cate-edit-page {
  width: 400px;
  position: fixed;
  top: 116px;
  left: 600px;
  z-index: 10;
  box-shadow: 0px 0px 26px #888888;
  background: white
}

.get-nav-input {
  margin: 10px 0 10px 0;
  margin-left: 10px;
  width: 100%
}
.create-category {
  display: flex;
  width: 100%;
  background: white;
}
.create-input {
  margin: 10px 0 10px 0;
  margin-left: 10px;
  width: 60%
}
.create-but {
  margin: 10px 10px 10px 0;
  margin-left: auto
}
</style>
