<template>
  <div class="container">
    <div style="">
      <!-- 分类选择 -->
      <div class="category">
        <div>
          <el-select v-model="navInputValue" @visible-change="clearCate" @change="GetSelCate" placeholder="请选择顶级导航">
            <el-option
              v-for="item in tableData"
              :key="item.tag"
              :label="item.name"
              :value="item">
            </el-option>
          </el-select>
        </div>
        <div class="cate-edit-but">
          <el-row>
            <el-button @click="cateEditBut" type="primary" plain>{{editButContent}}</el-button>
          </el-row>
        </div>
      </div>
      <!-- 分类编辑弹框 -->
      <div v-if="cateEditIsOpen" class="cate-edit-page">
        <!-- 分类编辑 -->
        <el-table
          :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
          style="width: 100%"
          ref="singleTable">
          <el-table-column
            label="Name"
            prop="name">
          </el-table-column>
          <el-table-column
            align="right">  
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder="输入关键字搜索"/>
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)">Edit</el-button>
              <el-popconfirm 
              title="确定删除？" @onConfirm="delNavigation">
                <el-button style="margin-left: 10px;" @click="handleDelete(scope.$index, scope.row)" size="mini" type="danger" slot="reference">Delete</el-button>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
        <div class="create-category">
          <div class="create-input">
            <el-input
              placeholder="请输入顶级导航名"
              v-model="crateCateInput"
              size="small"
              clearable>
            </el-input>
          </div>
          <div class="create-but">
            <el-button @click="postNavigation" size="small" type="primary" plain>创建</el-button>
            <!-- <el-button type="text" @click="open">创建分类</el-button> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { PostNavigation } from '@/api/api'
import { DelNavigation } from '@/api/api'
import { GetNavigation } from '@/api/api'
import util from '../../../utils/utils'

export default {
  data() {
    return {
      navInputValue: '', // 选中的导航数据
      editButContent: '编辑',
      crateCateInput: '',
      delCateContent: '', // 要删除的分类名称
      cateEditIsOpen: false,
      tableData: [], // 分类列表数据
      search: '', // 分类列表搜索框
    }
  },
  mounted () {
    // 编辑文章时获取的导航名称, 由Editor.vue触发
    util.$on('setNavInputValue', (item) => {
      this.navInputValue = item.navigation_name
    })

    // 获取导航列表
    this.getNavigation()

  },
  methods: {
    // 分类列表, 编辑按钮
    handleEdit(index, row) {
      console.log(index, row);
      //this.open2(row.name);
    },
    // 分类列表, 删除按钮
    handleDelete(index, row) {
      this.delCateContent = row
      //console.log(index, row);
    },
    delNavigation(){
      DelNavigation(this.delCateContent.id).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          this.successMsg()
          this.getNavigation()
        } else {
          this.errMsg('', response.data.msg)
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // 分类编辑弹框按钮
    cateEditBut() {
      if (this.cateEditIsOpen == true) {
        this.editButContent = '编辑'
        this.cateEditIsOpen = false
        return
      }
      if (this.cateEditIsOpen == false) {
        this.editButContent = '关闭'
        this.cateEditIsOpen = true
        return
      }
    },
    // 修改分类弹框
    open2(inputValue) {
      this.$prompt('请输入要修改的分类名', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputValue: inputValue
      }).then(({ value }) => {
        if ( value == '' ) { this.errMsg('不可为空','') } 
        else { console.log(value) }
      }).catch(() => {
        this.cancelMsg()
      });
    },
    // 获取导航
    getNavigation() {
      GetNavigation().then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          if (response.data.data.navigation == null) {
            this.tableData = []
            return
          }
          this.tableData = response.data.data.navigation
          return
        }
      }).catch(err => {
        this.errMsg('获取分类列表时出错', err)
      })
    },
    // 创建导航
    postNavigation() {
      var v = this.crateCateInput
      if ( v == '' ) { this.errMsg('分类名不可为空',''); return }

      PostNavigation(v).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          this.crateCateInput = ''
          this.successMsg()
          this.getNavigation()
        } else {
          this.errMsg('', response.data.msg)
        }
      }).catch(err => {
        this.errMsg('', err)
      })
    },
    // 获取选中的分类tag
    GetSelCate: function(item) {
      this.navInputValue = item.name // 选框的显示值,  疑问itme是哪里来的 上面并没有传
      util.$emit('getCateByNav', item.tag); // 传给同级Category.vue, 用于获取子分类
      this.$emit('GetSelNav', item); // 传给父组件
    },
    clearCate(e) {
      if (e == true) {
        util.$emit('clearCateByNav'); // 传给同级页面的分类
        this.navInputValue = ''
      }
    },
    // 提示消息
    successMsg() {
      this.$message({
        type: 'success',
        message: '操作成功'
      });
    },
    errMsg(msg, err) {
      this.$message({
        type: 'error',
        message: msg + err
      });
    },
    cancelMsg() {
      this.$message({
        type: 'info',
        message: '取消输入'
      });
    }
  },
}
</script>

<style scoped>
.container {
  width: 100%;
}
.category {
  display: flex;
  margin-bottom: 20px;
}
.cate-edit-but {
  /* div内的内容垂直居中 display: flex; align-items: center;*/
  display: flex;
  align-items: center;
  margin-left: 5px
}
.cate-edit-page {
  width: 400px;
  position: fixed;
  top: 116px;
  left: 249px;
  z-index: 10;
  box-shadow: 0px 0px 26px #888888;
  background: white
}
.create-category {
  display: flex;
  width: 100%;
  background: white;
}
.create-input {
  margin: 10px 0 10px 0;
  margin-left: 10px;
  width: 60%
}
.create-but {
  margin: 10px 10px 10px 0;
  margin-left: auto
}
</style>
